.casual{
    width:100%;
    height:100vh;
    display:flex;
    flex-direction:row;
    background: white;
    margin: 0px 20px;
    padding:7px;
}
.hero1, .hero3, .hero5{
    width:25%;
    height:90vh;
    background: white;
}
.hero2, .hero4{
    width:12%;
    height:90vh;
    background: white;
    display: flex;
    flex-direction:column;
}
.hero2a, .hero2b, .hero4a, .hero4b{
    width:100%;
    height:50%;
    background:rgb(154, 154, 192);
}
.casual-img{
    width:100%;
    height:100%;
    object-fit: cover;
    position: relative;
}
.right-ar, .left-ar{
    width:5%;
    height:100vh;
    z-index:1;
    position: absolute;  
    top: 50%;  
    left: 50%;
    transform: translate(-50%, -50%);
}